import React, { useMemo } from "react";
import tinyImg from "../../images/Tiny_Home.png";
import tiny2Img from "../../images/Tiny_Process.jpg";
import zanelliImg from "../../images/Zanelli_Home.png";
import bakersLaneImg from "../../images/BakersLane_Home.jpg";
import TagList from "../tagList";

const products = [
  {
    id: "tiny",
    title: "TINY - Ultraviolet Sunscreen Guard",
    tags: ["Sunscreen", "Lotion"],
    imgSrc: tinyImg,
    isPage: "index",
    isDual: false,
  },
  {
    id: "tiny-2",
    title: "TINY - Ultraviolet Sunscreen Guard",
    tags: ["Sunscreen", "Lotion"],
    imgSrc: tiny2Img,
    isPage: "work",
    isDual: false,
  },
  {
    id: "tiny-3",
    title: "TINY - Ultraviolet Sunscreen Guard",
    tags: ["Sunscreen", "Lotion"],
    imgSrcList: [tiny2Img, tiny2Img],
    isPage: "work",
    isDual: true,
  },
  {
    id: "zanelli",
    title: "Zenelli - The Pizza Room",
    tags: ["Italian Restaurant"],
    imgSrc: zanelliImg,
    isPage: "index",
    isDual: false,
  },
  {
    id: "zanelli-2",
    title: "Zenelli - The Pizza Room",
    tags: ["Italian Restaurant"],
    imgSrc: zanelliImg,
    isPage: "work",
    isDual: false,
  },
  {
    id: "bakersLane",
    title: "Bakers Lane - Mostly Baked",
    tags: ["Breads", "Bagels"],
    imgSrc: bakersLaneImg,
    isPage: "index",
    isDual: false,
  },
];

export default function HeroProductOverview({ isPage }) {
  const filteredProducts = useMemo(() => {
    return products.filter((e) => e.isPage === isPage);
  }, [isPage]);

  return (
    <>
      {filteredProducts.map((elem, idx) => {
        return (
          <div className="py-2 px-4" key={elem.id}>
            {!elem.isDual ? (
              <>
                <div className="row fw-bold">
                  <div className="col">
                    <p className="">{elem.title}</p>
                  </div>
                  <div className="col text-end">
                    <TagList tags={elem.tags} />
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <img
                      alt="Lane7"
                      src={elem.imgSrc}
                      width={"100%"}
                      className="d-inline-block align-text-top"
                    />
                  </div>
                </div>
              </>
            ) : (
              <div className="row">
                {elem.imgSrcList.map((imgSrc, idx) => {
                  return (
                    <div className="col" key={idx}>
                      <img
                        alt="Lane7"
                        src={imgSrc}
                        width={"100%"}
                        className="d-inline-block align-text-top"
                      />
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        );
      })}
    </>
  );
}
